	module.exports = {"pathContext":{"docHtml":"<h1>Polyfill for <code>Object.setPrototypeOf</code></h1>\n<p>A simple cross platform implementation to set the prototype of an instianted object.  Supports all modern browsers and at least back to IE8.</p>\n<h2>Usage:</h2>\n<pre><code>$ npm install --save setprototypeof\n</code></pre>\n<pre><code class=\"language-javascript\">var setPrototypeOf = require('setprototypeof');\n\nvar obj = {};\nsetPrototypeOf(obj, {\n    foo: function() {\n        return 'bar';\n    }\n});\nobj.foo(); // bar\n</code></pre>\n<p>TypeScript is also supported:</p>\n<pre><code class=\"language-typescript\">import setPrototypeOf = require('setprototypeof');\n</code></pre>","docPath":"argo-ci/node_modules/setprototypeof/readme","proj":"argo-ci"}}

